:root {
  background: rgb(219, 228, 251);
}

@media screen and (max-width: 1000px) {
  html, body {
    margin: 10px;
    padding: 0;
    background: rgb(219, 228, 251);
    height: 100%;
  }
}

@media screen and (min-width: 1000px) {
  html, body {
    margin: 20px;
    padding: 0;
    background: rgb(219, 228, 251);
    height: 100%;
  }
}

.site {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: repeat(9, auto);
  row-gap: 50px;
  grid-template-areas:
    "spacer1"
    "header"
    "spacer2"
    "welcome_message"
    "spacer3"
    "itinerary"
    "spacer4"
    "contact"
    "spacer5";
  /* margin: 20px 20px; */
