}

.site > * {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header_photo {
  grid-area: header_photo;
}
.couple_name {
  grid-area: couple_name;
}
.welcome_message {
  grid-area: welcome_message;
}
.couple_about {
  grid-area: couple_about;
}
.itinerary {
  grid-area: itinerary;
}
.countdown {
  grid-area: countdown;
}
.response {
  grid-area: response;
}
.contact {
  grid-area: contact;
}

.spacer1 {
  grid-area: spacer1;
}
.spacer2 {
  grid-area: spacer2;
}
.spacer3 {
  grid-area: spacer3;
}
.spacer4 {
  grid-area: spacer4;
}
.spacer5 {
  grid-area: spacer5;
}
.spacer6 {
  grid-area: spacer6;
}
